<template>
	<div id="shop-index">
		<public-header :title="title"></public-header>
		<public-footer :preLoading="showPreLoading" :menufooter="menufooter"></public-footer>
		<div class="container" :style="styleContent">
			<diy
				:diydata="diydata"
				:active = 'active'
				:is_grant = 'is_grant'
				:preLoading="showPreLoading"
				:goodsTabActive="goodsTabActive"
				:goodsTabFixed="goodsTabFixed"
				:showFixedSearchBar="showFixedSearchBar"
				@onChangeTab = "onChangeTab"
				@getCoupon = "getCoupon"
				@onCloseRedpacket="onCloseRedpacket"
				@onToggleGoodsTab="onToggleGoodsTab"
				@onChangeGoodsTabActive="onChangeGoodsTabActive"
			>
			</diy>
		</div>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import PublicFooter from '@/components/footer'
	import diy from '@/components/diy'
	import {mapState, mapMutations} from 'vuex'
	export default {
		data() {
			return {
				sid: 0,
				showPreLoading: true,
				styleContent: '#fff',
				is_grant: 0,
				title: '',
				active: 1,
				diydata: {
					diy: {
						data: {},
						fixedsearch: {},
						is_has_goodsTab: 0,
						is_has_location: 0
					},
					superRedpacketData: {
						errno: 0,
						message: {},
						is_show: true
					},
					config: {},

				},
				store: {},
				menufooter: {},
				storeCouponTop: 0,
				storeOnsaleTop: 0,
				storeEvaluateTop: 0,

				goodsTabActive: 0,
				goodsTabActiveReal: 0,
				goodsTabFixed: 0,
				goodsTabHeights: [],
				goodsTabOffsetTop: 0,
				followHeight: 0,
				scrollFromClickTab: false,
				showFixedSearchBar: false
			}
		},
		components: {
			PublicHeader,
			PublicFooter,
			diy,
		},
		computed: {
			...mapState([
				'istore', 'icart',
			])
		},
		methods: {
			...mapMutations([
				'setLocation', 'getLocation', 'replaceStore', 'replaceCart'
			]),

			onLoad() {
				this.util.request({
					url: 'wmall/shop/index',
					data: {
						sid: this.sid,
						menufooter: 1
					},
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message);
						return;
					}
					this.diydata.config = result.message.config_mall;
					this.diydata.diy = this.util.extend(this.diydata.diy, result.message.diy);
					this.styleContent = {
						'background-color': this.diydata.diy.data.page.background
					};
					this.diydata.superRedpacketData = result.message.superRedpacketData;
					this.diydata.superRedpacketData.is_show = true;
					this.title = this.diydata.diy.data.page.title || '门店首页';
					this.util.setWXTitle(this.title);
					this.menufooter = window.menufooter;

					this.cart = result.message.cart.message.cart;
					this.store = result.message.store;
					this.replaceStore(this.store);
					this.replaceCart(this.cart);
					this.store = result.message.store;
					this.$nextTick(() => {
						this.onCalculateStoreNavHeight();
						if(this.diydata.diy.is_has_goodsTab == 1) {
							this.onCalculateGoodsTabItemHeight();
						}
					})
				})
			},
			onChangeTab(index) {
				var that = this;
				if(index == 0) {
					var scrollTop = that.storeCouponTop - 46;
				} else if(index == 1) {
					var scrollTop = that.storeOnsaleTop - 46;
				} else if(index == 2) {
					var scrollTop = that.storeEvaluateTop - 46;
				}
				window.scrollTo(0, scrollTop)
			},
			getCoupon() {
				this.util.request({
					url: 'wmall/channel/coupon/get',
					data: {
						sid: this.sid
					},
				}).then((res) => {
					let result = res.data.message;
					if(result.errno == 0) {
						this.util.$toast(result.message, '', 1000);
						this.is_grant = 1;
					} else {
						this.util.$toast('领取失败', '', 1000);
					}
				})
			},

			onCloseRedpacket() {
				this.diydata.superRedpacketData.is_show = false;
				this.diydata.superRedpacketData = Object.assign({}, this.diydata.superRedpacketData);
			},

			// 计算商户详情页标签相关高度
			onCalculateStoreNavHeight() {
				var couponDom = document.getElementsByClassName('diy-store-coupon');
				if(couponDom && couponDom.length > 0) {
					this.storeCouponTop = document.getElementsByClassName('diy-store-coupon')[0].offsetTop;
				}
				var onsaleDom = document.getElementsByClassName('diy-store-onsale');
				if(onsaleDom && onsaleDom.length > 0) {
					this.storeOnsaleTop = document.getElementsByClassName('diy-store-onsale')[0].offsetTop;
				}
				var evaluateDom = document.getElementsByClassName('diy-store-evaluate');
				if(evaluateDom && evaluateDom.length > 0) {
					this.storeEvaluateTop = document.getElementsByClassName('diy-store-evaluate')[0].offsetTop;
				}
			},

			onToggleGoodsTab(data) {
				var that = this;
				that.scrollFromClickTab = true;
				var dtop = 44;

				var scrollTop = that.goodsTabOffsetTop - dtop;
				if(data.index >= 1) {
					scrollTop = that.goodsTabHeights[data.index - 1] - dtop;
				}

				scrollTop = scrollTop - that.followHeight;
				if(that.diydata.diy.is_has_location && scrollTop > 100) {
					that.goodsTabFixed = 2;
					scrollTop = scrollTop - 52;
				} else {
					if(that.util.isWeixin()) {
						that.goodsTabFixed = 1;
					} else {
						that.goodsTabFixed = 3;
						scrollTop = scrollTop - 46;
					}
				}
				window.scrollTo(0, scrollTop)
				that.goodsTabActive = data.index;
			},

			onCalculateGoodsTabItemHeight() {
				//goodstab组件 距离顶部的高度
				this.goodsTabOffsetTop = document.getElementsByClassName('goods-tab-inner')[0].offsetTop;
				//goodsTab自身高度修正
				this.goodsTabOffsetTop -= document.getElementsByClassName('van-tabs')[0].clientHeight - 44;
				//有立即关注follow时距离修正
				if(document.getElementById('follow')) {
					this.followHeight = document.getElementsByClassName('follow-tips')[0].clientHeight;
					this.goodsTabOffsetTop -= this.followHeight;
				}
				let goodsTabs = document.getElementsByClassName('goods-tab-item');
				let height = this.goodsTabOffsetTop;
				var heightArr = [];
				for (let i = 0; i < goodsTabs.length; i++) {
					let item = goodsTabs[i];
					height += item.clientHeight;
					heightArr.push(height);
				}
				this.goodsTabHeights = heightArr;
			},

			onChangeGoodsTabActive(data) {
				this.goodsTabActiveReal = data.value;
			},
		},
		created() {
			this.query = this.util.parseQuery(this.$route.query);
			this.sid = this.query.sid;
		},
		mounted() {
			var that = this;
			that.onLoad();
			window.addEventListener('scroll', function() {
				var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
				if(scrollTop > 100) {
					that.showFixedSearchBar = true;
				} else {
					that.showFixedSearchBar = false;
				}

				if(that.diydata.diy.is_has_goodsTab == 1) {
					var dtop = 44;
					if(!that.util.isWeixin()) {
						dtop = 44 + 46;
					}
					if(that.diydata.diy.is_has_location && that.showFixedSearchBar) {
						dtop = 44 + 52;
					}
					scrollTop = Math.ceil(scrollTop + dtop);
					if(scrollTop >= that.goodsTabOffsetTop) {
						if(that.diydata.diy.is_has_location && that.showFixedSearchBar) {
							that.goodsTabFixed = 2;
						} else {
							if(that.util.isWeixin()) {
								that.goodsTabFixed = 1;
							} else {
								that.goodsTabFixed = 3;
							}
						}
					} else {
						that.goodsTabFixed = 0;
					}

					if(!that.scrollFromClickTab) {
						var goodsTabHeights = that.goodsTabHeights;
						var length = goodsTabHeights.length;
						for(var i = 0; i < length; i++) {
							if(goodsTabHeights[i+1]) {
								if(scrollTop < goodsTabHeights[i] ) {
									that.goodsTabActive = i;
									break;
								} else if(scrollTop >= goodsTabHeights[i] && scrollTop < goodsTabHeights[i+1]) {
									that.goodsTabActive = i + 1;
									break;
								}
							} else {
								that.goodsTabActive = i;
								break;
							}
						}
					}
				}
				that.scrollFromClickTab = false;
			});
		}
	}
</script>

<style>

</style>


